<template>
	<view class="box">
		<view class="message" v-if="detail.title">
			<view class="title">{{detail.title}}</view>
			<view class="time">
				{{detail.createDate}}
			</view>
			<view class="content">{{detail.content}}</view>
		</view>
	</view>
</template>

<script>
	let seqId = ""
	export default {
		data() {
			return {
				detail:{}
			}
		},
		onLoad(option) {
			seqId = option.seqId;
			this.loadDetail()
		},
		methods: {
			loadDetail() {
				this.$api.get('/o2oNotifyHead/getByPk', {
					params: {
						seqId: seqId
					}
				}).then(res => {
					if(res.code==200){
						this.detail=res.data
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.box {
		width: 100%;
		min-height: calc(100vh - var(--window-top));
		background: #fff;

		.message {
			padding: 50rpx;

			.title {
				width: 100%;
				font-size: 36rpx;
			}

			.time {
				width: 100%;
				border-bottom: 1px solid #eee;
				font-size: 20rpx;
				line-height: 80rpx;
				color: #777;
			}

			.content {
				font-size: 28rpx;
				margin-top: 50rpx;
			}
		}
	}
</style>
